<template>
  <div class="navbar">
    <!-- Back Button -->
    <button v-if="showBackButton" class="btn btn-back" @click="goBack" aria-label="Go Back">
      Back
    </button>

    <!-- Dynamic Title -->
    <h2 class="title">{{ navbarTitle }}</h2>

    <!-- Add Character and Favorites Buttons -->
    <div v-if="showAddAndFavoritesButtons" class="navbar-buttons">
      <button v-if="showAddButton" class="btn btn-add" @click="navigateToAddCharacter">
        Add Character
      </button>
      <button class="btn btn-favorites" @click="navigateToFavorites" aria-label="View Favorites">
        Favorites
      </button>
    </div>
  </div>
</template>

<script>
export default {
  name: 'AppNavbar',
  computed: {
    showAddAndFavoritesButtons() {
      // Show Add and Favorites buttons on pages except AddCharacter and Favorites
      return this.$route.name !== 'AddCharacter' && this.$route.name !== 'CharacterFavorite';
    },
    showAddButton() {
      // Show Add Character button on pages except AddCharacter and Favorites
      return this.$route.name !== 'AddCharacter' && this.$route.name !== 'CharacterFavorite';
    },
    showBackButton() {
      // Show Back button on AddCharacter, Favorites, and CharacterDetails pages
      return ['AddCharacter', 'CharacterFavorite', 'CharacterDetails'].includes(this.$route.name);
    },
    navbarTitle() {
      // Set title based on current route
      if (this.$route.name === 'CharacterFavorite') {
        return 'Favorites';
      }
      return 'Character Finder v0.3.8';
    }
  },
  methods: {
    navigateToAddCharacter() {
      this.$router.push({ name: 'AddCharacter' });
    },
    navigateToFavorites() {
      this.$router.push({ name: 'CharacterFavorite' });
    },
    goBack() {
      this.$router.back();
    }
  }
};
</script>

