<template>
  <div class="character-list">
    <!-- Navbar -->
    <AppNavbar />

    <!-- Search bar -->
    <div class="search-bar flex-center gap-10 p-20 bg-secondary">
      <input
        v-model="searchQuery"
        type="text"
        placeholder="Search characters..."
        class="search-input w-500 font-medium"
        @input="debouncedSearchCharacters"
      />
      <button class="btn btn-search" @click="searchCharacters">Search</button>
    </div>

    <!-- Character Flex Container -->
    <div v-if="filteredCharacters.length" class="card-flex-container">
      <div
        v-for="(character, index) in filteredCharacters"
        :key="character.id"
        class="card copy-target"
        :data-clipboard-text="character.htmlCode"
        :data-index="index"
        tabindex="0"
        aria-label="Copy HTML code for {{ character.name }}"
      >
        <!-- Image Wrapper -->
        <div class="image-wrapper">
          <img :src="character.path" alt="Character Image" @error="handleImageError($event)" loading="lazy" />
          <div class="copy-overlay" v-if="copiedCardId === character.id">Copied!</div>
        </div>

        <!-- Card Content -->
        <div class="card-content">
          <h3>{{ character.name }}</h3>
          <div class="button-group">
            <!-- Favorite Button -->
            <button
              @click.stop="toggleFavorite(character)"
              class="btn btn-favorite"
              aria-label="Toggle favorite for {{ character.name }}"
            >
              <!-- Full Star SVG -->
              <svg
                v-if="isFavorite(character.id)"
                xmlns="http://www.w3.org/2000/svg"
                height="18px"
                viewBox="0 0 24 24"
                width="18px"
                fill="#ffa933"
              >
                <path d="M12 17.27L18.18 21l-1.64-7.03L22 9.24l-7.19-.62L12 2 9.19 8.63 2 9.24l5.46 4.73L5.82 21z"/>
              </svg>
              <!-- Empty Star SVG -->
              <svg
                v-else
                xmlns="http://www.w3.org/2000/svg"
                height="18px"
                viewBox="0 0 24 24"
                width="18px"
                fill="#ffa933"
              >
                <path d="M22 9.24l-7.19-.62L12 2 9.19 8.63 2 9.24l5.46 4.73L5.82 21 12 17.27 18.18 21l-1.64-7.03L22 9.24zm-10 6.14l-3.76 2.27 1-4.28L5.5 10.5l4.38-.38L12 6l2.12 4.12 4.38.38-3.24 3.63 1 4.28L12 15.38z"/>
              </svg>
            </button>

            <!-- Info Button -->
            <button
                @click.stop="openCharacterDetails(character)"
                class="btn btn-info btn-info-custom"
                aria-label="More information about {{ character.name }}"
              >
                <!-- Updated Info SVG with Contrasting Fill -->
                <svg xmlns="http://www.w3.org/2000/svg" height="18px" viewBox="0 -960 960 960" width="18px" fill="#2980b9">
                  <path
                    d="M440-280h80v-240h-80v240Zm40-320q17 0 28.5-11.5T520-640q0-17-11.5-28.5T480-680q-17 0-28.5 11.5T440-640q0 17 11.5 28.5T480-600Zm0 520q-83 0-156-31.5T197-197q-54-54-85.5-127T80-480q0-83 31.5-156T197-763q54-54 127-85.5T480-880q83 0 156 31.5T763-763q54 54 85.5 127T880-480q0 83-31.5 156T763-197q-54 54-127 85.5T480-80Zm0-80q134 0 227-93t93-227q0-134-93-227t-227-93q-134 0-227 93t-93 227q0 134 93 227t227 93Zm0-320Z"
                  />
                </svg>
              </button>
          </div>
        </div>
      </div>
    </div>

    <!-- No Characters Found Message -->
    <div v-else-if="searchQuery.trim()" class="no-characters">
      <p>No characters found. Try adjusting your search.</p>
    </div>

    <!-- Prompt to Search Message -->
    <div v-else class="all-characters">
      <p>To find an image, please search for a tag.</p>
    </div>
  </div>
</template>

<script>
import Clipboard from 'clipboard';
import { get, ref, remove, set } from 'firebase/database';
import { db } from '../firebase';
import AppNavbar from './AppNavbar.vue';

export default {
  components: {
    AppNavbar,
  },
  data() {
    return {
      searchQuery: '',
      characters: [],
      filteredCharacters: [],
      copiedCardId: null,
      clipboard: null,
      debounceTimer: null,
      favorites: [], // List of favorite character IDs
    };
  },
  methods: {
    openCharacterDetails(character) {
      this.$router.push({
        name: 'CharacterDetails',
        params: { id: character.id },
      });
    },
    debouncedSearchCharacters() {
      clearTimeout(this.debounceTimer);
      this.debounceTimer = setTimeout(this.searchCharacters, 300);
    },
    async searchCharacters() {
      const query = this.searchQuery.trim();
      if (query === '') {
        this.filteredCharacters = [];
        sessionStorage.removeItem('searchQuery');
        return;
      }

      sessionStorage.setItem('searchQuery', this.searchQuery);

      const queryTerms = query
        .toLowerCase()
        .split(',')
        .map((term) => term.trim())
        .filter((term) => term !== '');

      this.filteredCharacters = this.characters.filter((character) => {
        const matchesName = queryTerms.some((term) =>
          character.name.toLowerCase().includes(term)
        );
        const matchesTags =
          character.tags &&
          character.tags.some((tag) =>
            queryTerms.some((query) => tag.toLowerCase().includes(query))
          );
        return matchesName || matchesTags;
      });

      this.$nextTick(() => {
        this.initializeClipboard();
      });
    },
    async fetchCharacters() {
      const characterRef = ref(db, 'characters/');
      try {
        const snapshot = await get(characterRef);
        if (snapshot.exists()) {
          this.characters = Object.entries(snapshot.val()).map(([key, value]) => ({
            id: key,
            ...value,
          }));
          this.applySearchQuery();
        } else {
          console.log('No characters found in the database.');
        }
      } catch (error) {
        console.error('Error fetching characters:', error);
      }
    },
    applySearchQuery() {
      const savedSearchQuery = sessionStorage.getItem('searchQuery');
      if (savedSearchQuery) {
        this.searchQuery = savedSearchQuery;
        this.searchCharacters();
      } else {
        this.filteredCharacters = [];
      }
    },
    async toggleFavorite(character) {
      try {
        const favoriteRef = ref(db, `favorites/${character.id}`);
        if (this.isFavorite(character.id)) {
          // Remove from favorites
          await remove(favoriteRef);
          this.favorites = this.favorites.filter((id) => id !== character.id);
        } else {
          // Add to favorites
          await set(favoriteRef, true);
          this.favorites.push(character.id);
        }
        // Refresh the Clipboard
        this.searchCharacters();
      } catch (error) {
        console.error('Error toggling favorite:', error);
      }
    },
    isFavorite(characterId) {
      return this.favorites.includes(characterId);
    },
    async fetchFavorites() {
      try {
        const favoritesRef = ref(db, 'favorites/');
        const snapshot = await get(favoritesRef);
        if (snapshot.exists()) {
          this.favorites = Object.keys(snapshot.val());
        } else {
          this.favorites = [];
        }
      } catch (error) {
        console.error('Error fetching favorites:', error);
      }
    },
    handleCopySuccess(e) {
      const index = parseInt(e.trigger.getAttribute('data-index'));
      const copiedCharacter = this.filteredCharacters[index];
      if (copiedCharacter) {
        this.copiedCardId = copiedCharacter.id;
        setTimeout(() => {
          this.copiedCardId = null;
        }, 2000);
      }
      e.clearSelection();
    },
    handleCopyError(e) {
      console.error('Clipboard copy failed:', e);
      alert('Failed to copy HTML Code.');
    },
    initializeClipboard() {
      if (this.clipboard) {
        this.clipboard.destroy();
      }

      if (this.filteredCharacters.length > 0) {
        this.clipboard = new Clipboard('.copy-target');

        this.clipboard.on('success', this.handleCopySuccess);
        this.clipboard.on('error', this.handleCopyError);
      }
    },
    handleImageError(event) {
      event.target.src = 'https://via.placeholder.com/300x300?text=Image+Not+Found';
    },
  },
  async created() {
    await this.fetchFavorites();
  },
  watch: {
    characters() {
      this.applySearchQuery();
    },
  },
  mounted() {
    this.fetchCharacters();
  },
  beforeUnmount() {
    if (this.clipboard) {
      this.clipboard.destroy();
    }
  },
};
</script>

